.profile__avatar-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    cursor: pointer;
    border: none;
    opacity: 0;
    transition: opacity .2s ease;
}

.profile__avatar-btn:hover {
    opacity: 1;
}